/**
 * å°è£…getæ–¹æ³•
 * @param url  è¯·æ±‚url
 * @param params  è¯·æ±‚å‚æ•°
 * @returns {Promise}
 */
export function get(url: string, params = {}) {
  return new Promise((resolve, reject) => {
    axios
      .get(url, {
        params: params,
      })
      .then((response) => {
        resolve(response.data);
      })
      .catch((error) => {
        reject(error);
      });
  });
}

/**
 * å°è£…postè¯·æ±‚
 * @param url
 * @param data
 * @returns {Promise}
 */

export function post(url: string, data: any) {
  return new Promise((resolve, reject) => {
    axios
      .post(url, data)
      .then(
        (response) => {
          resolve(response.data);
        },
        (err) => {
          reject(err);
        }
      );
  });
}

